import {GroupsModel} from './GroupsModel';
import {BlocksModel} from './BlocksModel';
export class Serializable {
    fillFromJSON(json: string) {
        var jsonObj = JSON.parse(json).array.forEach(element => {
            console.log(element);
        });
        for (var propName in jsonObj) {
            this[propName] = jsonObj[propName]
        }
    }
}

export class BlockGroupsModel extends Serializable {
     group: GroupsModel;
     blocks: BlocksModel[];
    constructor( group, 
    blocks)
    {
        super();
        this.group = group;
        this.blocks = blocks;
        
    }
    GetGroup(): GroupsModel { return this.group }
    GetBlocks():BlocksModel[] {return this.blocks}
}



