import 'messenger/build/js/messenger.js';
import 'jquery-ui/ui/sortable.js';
import 'jquery.nestable/jquery.nestable.js';

import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { AlertModule, TooltipModule } from 'ng2-bootstrap/ng2-bootstrap';
import { ButtonsModule, DropdownModule } from 'ng2-bootstrap/ng2-bootstrap';
import { TabsModule, AccordionModule } from 'ng2-bootstrap/ng2-bootstrap';
import { ModalModule } from 'ng2-modal';
import { Specific } from './specific.component';
import { WidgetModule } from '../layout/widget/widget.module';
import { Select2Module } from 'ng2-select2';
import { QuillModule } from './../directory/quill/quill.module';
//import {Autosize} from 'angular2-autosize';
import {AutoModule} from './../generic/auto/autosize.module';
//import { AutoModule } from './../generic/auto/autosize.module';
export const routes = [
  { path: '', component: Specific, pathMatch: 'full' }
  
];

@NgModule({
  declarations: [
    // Components / Directives/ Pipes
    Specific,
  ],
  imports: [
    AutoModule,
    CommonModule,
    FormsModule,
    RouterModule.forChild(routes),
    AlertModule,
    WidgetModule,
    TooltipModule,
    ModalModule,
    ButtonsModule,
    DropdownModule,
    TabsModule,
    AccordionModule,
    Select2Module,
    QuillModule
  ]
})
export default class SpecificModule {
  static routes = routes;
}



