import {CardAddButton} from './CardAddButton';
export class Serializable {
    fillFromJSON(json: string) {
        var jsonObj = JSON.parse(json).array.forEach(element => {
            console.log(element);
        });
        for (var propName in jsonObj) {
            this[propName] = jsonObj[propName]
        }
    }
}

export class QuickReplyCard extends Serializable {
     quickReplyBtns: CardAddButton[];
    constructor( _quickReplyButton)
    {
        super();
        this.quickReplyBtns = _quickReplyButton;
    }
    GetQuickReplyButtons(): CardAddButton[] { return this.quickReplyBtns }
}



