        this._botTrainingService.deleteBlock(this.deleteBlockId)
            .subscribe(
            a => {
                if (a.code == 200) {
                    for (var i = 0; i < this.blockGroupsModel.length; i++) {
                        for (var j = 0; j < this.blockGroupsModel[i].blocks.length; j++) {
                            if (this.blockGroupsModel[i].blocks[j]._id == this.deleteBlockId) {
                                this.blockGroupsModel[i].blocks.splice(j, 1);
                                break;
                            }
                        }
                    }
                    console.log(this.select2GroupedData);
                    for (var i = 0; i < this.select2GroupedData.length; i++) {
                        if (this.select2GroupedData[i].id == this.deleteBlockId) {
                            this.select2GroupedData.splice(i, 1);
                        }
                    }
                    this.blockConfirmationModal.close();
                }
            }
            );
