        this._botTrainingService.deleteRandomText(this.randomTextId, this.cardId).subscribe(
            a => {
                if (a.code == 200) {
                    this.blockDetail(this.popBlock._id);
                    this.randomTextCardModal.close();
                    if (a.data == true) {
                        this.blockIsCompletedStatusChange(this.popBlock._id, true);
                    }
                    else {
                        this.blockIsCompletedStatusChange(this.popBlock._id, false);
                    }
                }
            }
        );
