        this._botTrainingService.deleteAddButton(this.addButtonId, this.cardId, this.cardType).subscribe(
            a => {
                if (a.code == 200) {
                    this.blockDetail(this.popBlock._id);
                    this.addBtnModal.close()
                }
            }
        );
